import React, { useState } from 'react';
import {
    Box,
    Button,
    Columns,
    Container,
    Dropdown,
    Heading,
    Level,
    Message,
    Modal,
} from 'react-bulma-components';
import {
    Map,
    Marker,
    TileLayer
} from 'react-leaflet';
import { contentCheck } from '../components/contentCheck.js'
import ClimbingGrade from 'climbing-grade';

import NamedField from '../components/NamedField.js';
import NamedFieldLevel from '../components/NamedFieldLevel.js';
import NamedParagraph from '../components/NamedParagraph.js';
import { withRouter } from 'react-router';

import './css/flyoutDetailsColumn.css';

const FlyoutDetailsColumn = ( props ) => {
    //Declarations
    const [ showMap, setShowMap ] = useState(false);
    const [ displayGradeSystem, setDisplayGradeSystem ] = useState('yds');
    const [ deletePressed, setDeletePressed] = useState(false);

    const gradeInfo = props.reportInfo ? new ClimbingGrade( props.reportInfo.grade, props.reportInfo.gradeSystem ) : null;

    const visibility = props.show ? 'show' : 'hide';

    //Event Handlers
    const handleDeleteReport = async () => {
        await fetch(`/api/${props.user}/picture-trip-reports/${props.reportInfo.id}/delete`, {
            method: 'post',
        });
    }

    //Conditional Renders
    const deleteMessage = deletePressed ?
        <Message color='danger'>
            <Message.Header>
                Are you sure?
                <Button color='danger' onClick={()=>{
                    handleDeleteReport();
                    props.history.push(`/tom-novak/trip-reports`);
                }}>Delete</Button>
                <Button remove onClick={()=>setDeletePressed(false)}/>
            </Message.Header>
        </Message> : null;


    return (
        <Columns.Column id='flyout-detail-column' className={visibility}>
            <Box id='flyout-title-box'>
                <Heading align='left' className='is-size-4' style={{'margin': '0px 0px 5px 0px'}}>{props.reportInfo.title}</Heading>
                <Heading align='left' className='is-size-6' style={{'margin': '0px 0px 0px 0px'}}>{props.reportInfo.author}</Heading>
            </Box>
            <Box id='flyout-detail-box' style={{height: `calc( ${props.height} + 11vh )`}} >
                <NamedFieldLevel name="Route" content={contentCheck(props.reportInfo.route)} />
                <NamedFieldLevel name="Location" content={contentCheck(props.reportInfo.loc)} />
                <Level>
                    <Level.Side align='right'>
                        <Level.Item>
                            <Heading heading className="has-text-weight-bold">Coordinates</Heading>
                        </Level.Item>
                        <Level.Item>
                            <Heading heading>{contentCheck(props.reportInfo.coord[0])}, {contentCheck(props.reportInfo.coord[1])}</Heading>
                        </Level.Item>
                    </Level.Side>
                    <Level.Side align='right'>
                        <Level.Item>
                            <Button color='primary' onClick={()=>setShowMap(true)}>Show on map</Button>
                            <Modal closeOnBlur={true} showClose={false} show={showMap} onClose={()=>setShowMap(false)}>
                                <Modal.Content>
                                    <Container>
                                        <Map id="modal-map" center={[props.reportInfo.coord[0], props.reportInfo.coord[1]]} zoom={12}>
                                            <TileLayer
                                                url="https://api.mapbox.com/styles/v1/{id}/tiles/{z}/{x}/{y}?access_token={accessToken}"
                                                attribution='Map data  <a href="https://www.openstreetmap.org/">OpenStreetMap</a> contributors, <a href="https://creativecommons.org/licenses/by-sa/2.0/">CC-BY-SA</a>, Imagery (c) <a href="https://www.mapbox.com/">Mapbox</a>'
                                                maxZoom={18}
                                                id='mapbox/outdoors-v11'
                                                accessToken= 'pk.eyJ1IjoidG9tbm92YWsxMyIsImEiOiJja2VseGtxeGwyYmU0MnJtejdiZmIydHd2In0.Wx7ng-I076vOfOb2qNnTkA'
                                            />
                                            <Marker position={[props.reportInfo.coord[0], props.reportInfo.coord[1]]} />
                                        </Map>
                                    </Container>
                                </Modal.Content>
                            </Modal>
                        </Level.Item>
                    </Level.Side>
                </Level>

                <Columns>
                    <Columns.Column size="half">
                        <Level id="centered-level-item">
                            <Level.Item>
                                <NamedField name="Discipline" content={contentCheck(props.reportInfo.disc)} />
                            </Level.Item>
                            <Level.Item>
                                <NamedField name="Grade" content={gradeInfo ? gradeInfo.format(displayGradeSystem) + " " +  props.reportInfo.sev : "\u2014"} />
                            </Level.Item>
                        </Level>
                    </Columns.Column>
                    <Columns.Column align='right' size="half">
                        <Dropdown 
                            hoverable
                            right
                            value={displayGradeSystem}
                            onChange={(value)=>{setDisplayGradeSystem(value)}}>
                            <Dropdown.Item value='yds'>
                                YDS
                            </Dropdown.Item>
                            <Dropdown.Item value='french'>
                                French
                            </Dropdown.Item>    
                            <Dropdown.Item value='australian'>
                                Australian
                            </Dropdown.Item>
                            <Dropdown.Item value='british'>
                                British
                            </Dropdown.Item>
                            <Dropdown.Item value='south_african'>
                                South African
                            </Dropdown.Item>
                            <Dropdown.Item value='hueco'>
                                Hueco
                            </Dropdown.Item>
                            <Dropdown.Item value='font'>
                                Fontainebleau
                            </Dropdown.Item> 
                        </Dropdown>
                    </Columns.Column>   
                </Columns>

                <Level id="centered-level-item">
                    <Level.Item>
                        <NamedField name="Pitches" content={contentCheck(props.reportInfo.pitches)} />
                    </Level.Item>
                    <Level.Item>
                        <NamedField name="Total Length (m)" content={contentCheck(props.reportInfo.totalLength)} />
                    </Level.Item>
                    <Level.Item>
                        <NamedField name="Elevation (m)" content={contentCheck(props.reportInfo.elev)} />
                    </Level.Item>
                    <Level.Item>
                        <NamedField name="Aspect" content={contentCheck(props.reportInfo.aspect)} />
                    </Level.Item>
                </Level>

                <Box>
                    <NamedParagraph name="Gear" content={props.reportInfo.gear} />
                </Box>

                <Box>
                    <NamedParagraph name="Approach" content={props.reportInfo.approach} />
                </Box>

                <Box>
                    <NamedParagraph name="Descent" content={props.reportInfo.descent} />
                </Box>

                <Box style={{'margin-bottom': '15px'}}>
                    <NamedParagraph name="Conditions" content={props.reportInfo.conditions} />
                </Box>
                
                <Box>
                    {deleteMessage}
                    <Level>
                        <Level.Item>
                            <Button onClick={()=>props.history.push(`/tom-novak/edit-photo-report/${props.reportInfo.id}`)} color='primary'>Edit Report</Button>
                        </Level.Item>
                        <Level.Item>
                            <Button onClick={()=>setDeletePressed(true)} color='danger'>Delete</Button>
                        </Level.Item>
                    </Level>
                </Box>
            </Box>
        </Columns.Column>
    );
}

export default withRouter(FlyoutDetailsColumn);