import React, { createRef } from 'react';
import '../App.css'
import {
    Container,
    Heading,
    Hero,
    List,
} from 'react-bulma-components';
import { sortReports } from './helpers/reportOperators';
import LogListEntry from './LogListEntry';
import AnimateChildren from './AnimateChildren';

const ReportsList = ({userInfo, setUserInfo, sortBy='date'}) => {
    //Initialize report array
    const reports = userInfo.pitchReports;

    const sortedReports = sortReports(userInfo.pitchReports, sortBy);
    //Render
    return(
        reports.length === 0 ? 
            <Hero color="primary" size="small">
                <Hero.Body>
                    <Container>
                    <Heading size={3} align="center">
                        No reports added! Click 'Add Report' to get started.
                    </Heading>
                    </Container>
                </Hero.Body>
            </Hero> :
            <List>
                <AnimateChildren>
                {sortedReports.map((report) => (
                    <div key={report.title} ref={createRef()}>
                        <LogListEntry key={report.title}
                            user={userInfo.user}
                            report={report}
                            setUserInfo={setUserInfo}
                        />
                    </div>
                    ))}
                </AnimateChildren>
 


            </List>
    );
}

export default ReportsList;