import React, { useState, useEffect } from 'react';
import { Map, Marker, TileLayer } from 'react-leaflet';
import { formatTripID } from '../components/helpers/formatParser.js';
import {
    Box,
    Button,
    Columns,
    Dropdown,
    Form,
    Heading,
    Icon,
    Image,
    List,
    Level,
    Message
} from 'react-bulma-components';
import ClimbingGrade from 'climbing-grade';
import DatePicker from 'react-date-picker';
import { contentCheck } from '../components/contentCheck.js'
import AddPitchBox from '../components/AddPitchBox.js'
import ClimbingPitch from '../components/helpers/ClimbingPitch.js';
import { Link, withRouter } from 'react-router-dom';
import './css/addReportPage.css'
import { getS3Image, uploadS3Image } from '../components/helpers/s3Interface.js';
const { Input, Field, Control, Label, Textarea, InputFile } = Form;

const unique = (value, index, self) => {
    return self.indexOf(value) === index
  }

const EditPitchReportPage = (props) => {
    //Declarations
    const user      = props.match.params.user;
    const reportID  = props.match.params.reportID;
    const [ title, setTitle ] =             useState('');
    const [ author, setAuthor ] =           useState('');
    const [ description, setDescription ] = useState('');
    const [ coverImage, setCoverImage ] = useState(null);
    const [ coordinates, setCoordinates ] = useState([]);
    const [ location, setLocation ] =       useState('');
    const [ route, setRoute ] =             useState('');
    const [ pitches, setPitches ] =         useState('');
    const [ gear, setGear ] =               useState('');
    const [ elevation, setElevation ] =     useState('');
    const [ unitState, setUnitState ] =     useState('m');
    const [ date, setDate ] =               useState(new Date());
    const [ totalTime, setTotalTime ] =     useState('');
    const [ totalLength, setTotalLength ] = useState('');
    const [ discipline, setDiscipline ] =   useState('Traditional');
    const [ anchors, setAnchors ] =         useState('');
    const [ aspect, setAspect ] =           useState('');
    const [ approach, setApproach ] =       useState('');
    const [ descent, setDescent ] =         useState('');
    const [ conditions, setConditions ] =   useState('');
    const [ severity, setSeverity ] =       useState(' ');
    const [ gradeInfo, setGradeInfo ] =     useState(new ClimbingGrade('5.10a', 'yds'));

    const [ mapState, setMapState ] =       useState('Manual');
    const [ errorAlert, setErrorAlert ] =   useState(false);

    const [ upvotes, setUpvotes ] =         useState(0);
       
    const [pitchArray, setPitchArray ] = useState([]);

    const [firstLoad, setFirstLoad] = useState(true);

    //Event Handlers
    useEffect(() => {
        const fillFields = (report) => {
            setTitle(report.title);
            setAuthor(report.author);
            setDescription(report.desc);
            setCoverImage(report.coverImg);
            setCoordinates(report.coord);
            setLocation(report.loc);
            setRoute(report.route);
            setPitches(report.pitches);
            setGear(report.gear);
            setElevation(report.elev);
            setDate(report.date);
            setTotalTime(report.totalTime);
            setDiscipline(report.disc);
            setAnchors(report.anchors);
            setAspect(report.aspect);
            setApproach(report.approach);
            setDescent(report.descent);
            setConditions(report.conditions);
            setSeverity(report.sev);
            setGradeInfo(new ClimbingGrade(report.grade, report.gradeSystem));
            setUpvotes(report.upvotes);

            //handle copy of pitch array
            let workingArray = [];
            report.pitchArray.map((pitch, index)=> {
                workingArray[index] = pitch ? new ClimbingPitch(pitch.number, new ClimbingGrade(pitch.grade._inputGrade, report.gradeSystem), pitch.length, pitch.images, pitch.captions) : null;
                return 0;
            })
            setPitchArray(workingArray);
        }

        const fetchData = async () => {
            const result = await fetch(`/${user}/picture-trip-reports/${reportID}`);
            const reportInfo = await result.json();

            fillFields(reportInfo);
        }
        fetchData();
    }, [user, reportID]);

    const handlePitchChange = (pitch, index) => {
        let newPitchArray = [...pitchArray];
        newPitchArray[index] = pitch;
        setPitchArray(newPitchArray);
    }
    
    const handlePitchDelete = (index) => {
        let newPitchArray = [...pitchArray];
        newPitchArray[index] = null;
        setPitchArray(newPitchArray);
    }
    
    const handleUpdate = async () => {
        if(title.length === 0) {
            setErrorAlert(true);
        } else {

            await fetch(`/api/${user}/picture-trip-reports/${reportID}/update`, {
                method: 'post',
                body: JSON.stringify({
                    report: {
                        //descriptive
                        title:      contentCheck(title),
                        author:     contentCheck(author),
                        id:         contentCheck(formatTripID(title)),
                        date:       contentCheck(date),
                        desc:       description,
                        coverImg:   coverImage,
                        totalTime:  contentCheck(totalTime),

                        //pitch-by-pitch
                        pitchArray:     pitchArray,

                        //details
                        route:          contentCheck(route),
                        loc:            contentCheck(location),
                        coord:          [contentCheck(coordinates[0]), contentCheck(coordinates[1])],
                        pitches:        contentCheck(pitches),
                        totalLength:    contentCheck(totalLength),
                        disc:           contentCheck(discipline),
                        grade:          gradeInfo._inputGrade,
                        gradeSystem:    gradeInfo._inputSystemName,
                        sev:            contentCheck(severity, ""),
                        gear:           gear,
                        anchors:        anchors,
                        elev:           contentCheck(elevation),
                        aspect:         contentCheck(aspect),
                        approach:       approach,
                        descent:        descent,
                        conditions:     conditions,


                        //upvotes & comments
                        upvotes: upvotes
                    }
                }),
                headers: {
                    'Content-Type': 'application/json',
                }
            });
            props.history.push(`/tom-novak/photo-report/${reportID}`);
        }
    }

    if( coverImage && firstLoad ){
        getS3Image(coverImage).then(img => setCoverImage(img));
        setFirstLoad(false);
    }

    //Conditional Renders
    const coverPicture = coverImage && coverImage.startsWith('blob') ? <Image width='100%' src={coverImage}/> : null;

    const pitchContent = pitchArray.length > 0 ?
        <List>
            {pitchArray.map((pitch, key) => pitch ?
            <List.Item key={key}>
                <AddPitchBox pitch={pitch} index={key} setParent={handlePitchChange} deletePitch={handlePitchDelete} gradeSystem={gradeInfo._inputSystemName}/>
            </List.Item> : null
            )}
        </List> : null;

    const coordinateLevelLeft = mapState === 'Manual' ? 
        <Level.Side align="left">
            <Level.Item>
                <Control>
                    <Input
                        placeholder="Latitude"
                        value={coordinates[0]} 
                        onChange={(event)=> setCoordinates([event.target.value, coordinates[1]])}/>
                </Control>
            </Level.Item>
            <Level.Item>
                <Control>
                    <Input 
                        placeholder="Longitude" 
                        value={coordinates[1]} 
                        onChange={(event)=> setCoordinates([coordinates[0], event.target.value])}/>
                </Control>
            </Level.Item>
        </Level.Side>
        : 
        <Level.Side align="left">
            <Level.Item>
                <Heading weight="normal" size={5}>Latitude:</Heading>
            </Level.Item>
            <Level.Item>
                <Heading size={5}>{coordinates[0]}</Heading>
            </Level.Item>
            <Level.Item>
                <Heading weight="normal" size={5}>Longitude:</Heading>
            </Level.Item>
            <Level.Item>
                <Heading size={5}>{coordinates[1]}</Heading>
            </Level.Item>
        </Level.Side>
    ;

    const errorMessage = errorAlert ? 
        <Message color='info'>
            <Message.Header>
                Submit Failure
                <Button remove onClick={()=>setErrorAlert(false)}/>
            </Message.Header>
            <Message.Body>
                Please add a title to your trip before submitting
            </Message.Body>
        </Message> : null;
                
    const coordinateMarker = coordinates[0] && coordinates[1] ? 
        <Marker position={coordinates}/> : null;

    const mapCoordinateEntry = mapState === 'Map' ?
        <Map onClick={(e)=>{setCoordinates([e.latlng.lat.toFixed(3), e.latlng.lng.toFixed(3)])}} className="add-report-map" center={[51.09, -115.34]} zoom={12}>
            <TileLayer
                url="https://api.mapbox.com/styles/v1/{id}/tiles/{z}/{x}/{y}?access_token={accessToken}"
                attribution='Map data  <a href="https://www.openstreetmap.org/">OpenStreetMap</a> contributors, <a href="https://creativecommons.org/licenses/by-sa/2.0/">CC-BY-SA</a>, Imagery (c) <a href="https://www.mapbox.com/">Mapbox</a>'
                maxZoom={18}
                id='mapbox/outdoors-v11'
                accessToken= 'pk.eyJ1IjoidG9tbm92YWsxMyIsImEiOiJja2VseGtxeGwyYmU0MnJtejdiZmIydHd2In0.Wx7ng-I076vOfOb2qNnTkA'
            />
            {coordinateMarker}
        </Map> : null;

    //Render
    return (
        <Box>
                <Columns className="has-text-left">
                    <Columns.Column/>
                    <Columns.Column size="two-thirds">
                        
                        <Heading subtitle size={2}><strong>Report Summary</strong></Heading>

                        <Box>
                            <Field>
                                <Label>Title</Label>
                                <Control>
                                    <Input placeholder="Mt Assiniboine" value={title} onChange={(event)=> setTitle(event.target.value)}/>
                                </Control>
                            </Field>

                            <Field>
                                <Label>Author</Label>
                                <Control>
                                    <Input placeholder="John Smith" value={author} onChange={(event)=> setAuthor(event.target.value)}/>
                                </Control>
                            </Field>

                            <Level>
                                <Level.Side align="left">
                                    <Level.Item>
                                        <Field>
                                            <Label>Date</Label>
                                            <DatePicker onChange={(value)=>{setDate(value)}} value={date}/>
                                        </Field>
                                    </Level.Item>
                                </Level.Side>
                                <Level.Side align="right">
                                    <Level.Item>
                                        <Field>
                                            <Label>Time Taken (hrs)</Label>
                                            <Control>
                                                <Input placeholder="8.5" value={totalTime} onChange={(event)=>setTotalTime(event.target.value)}/>
                                            </Control>
                                        </Field>
                                    </Level.Item>
                                </Level.Side>
                            </Level>

                            <Field>
                                <Label>Summary</Label>
                                <Control>
                                    <Textarea placeholder="Description" value={description} onChange={(event)=> setDescription(event.target.value)}/>
                                </Control>
                            </Field>

                            <Label>Cover Photo</Label>
                                {coverPicture}

                            <Field>
                                <InputFile
                                    icon={<Icon icon='upload' />}
                                    boxed
                                    onChange={(event)=>{setCoverImage(event.target.files[0].name); uploadS3Image(event.target.files);}} />
                            </Field>

                        </Box>

                        <Heading subtitle size={2}><strong>Pitch-by-Pitch Details</strong></Heading>

                        <Box>
                            {pitchContent}
                            <Button
                            onClick={() => setPitchArray(pitchArray.concat(new ClimbingPitch(pitchArray.length)))}>
                                Add Pitch
                            </Button>
                        </Box>



                        <Heading subtitle size={2}><strong>Route Details</strong></Heading>

                        <Box>

                            <Columns>
                                <Columns.Column size="half">
                                    <Field>
                                        <Label>Route</Label>
                                        <Control>
                                            <Input placeholder="North Ridge" value={route} onChange={(event)=> setRoute(event.target.value)}/>
                                        </Control>
                                    </Field>
                                </Columns.Column>
                                <Columns.Column size="half">
                                    <Field>
                                        <Label>Location</Label>
                                        <Control>
                                            <Input placeholder="Assiniboine Provincial Park" value={location} onChange={(event)=> setLocation(event.target.value)}/>
                                        </Control>
                                    </Field>
                                </Columns.Column>    
                            </Columns>

                            <Box>
                                <Field>
                                    <Label>Coordinates</Label>
                                    <Level>
                                        {coordinateLevelLeft}
                                        <Level.Side align="right">
                                            <Level.Item>
                                                <Field kind="addons">
                                                    <Control>
                                                        <Button
                                                            color={mapState === 'Map' ? "primary" : ""}
                                                            onClick={() => setMapState('Map')}>
                                                            Map
                                                        </Button>
                                                    </Control>
                                                    <Control>
                                                        <Button
                                                            color={mapState === 'Manual' ? "primary" : ""}
                                                            onClick={() => setMapState('Manual')}>
                                                            Manual
                                                        </Button>
                                                    </Control>
                                                </Field>
                                            </Level.Item>
                                        </Level.Side>
                                    </Level>
                                    {mapCoordinateEntry}
                                </Field>
                            </Box>


                            <Level>
                                <Level.Side id="level-shrink" align="left">
                                    <Level.Item id="level-shrink">
                                        <Box>
                                            <Field>
                                                <Label>Pitches</Label>
                                                <Control>
                                                    <Input placeholder="N/A" value={pitches} onChange={(event)=>setPitches(event.target.value)}/>
                                                </Control>
                                            </Field>
                                        </Box>
                                    </Level.Item>
                                    <Level.Item id="level-shrink">
                                        <Box>
                                            <Field>
                                                <Label>Total Metres</Label>
                                                <Control>
                                                    <Input placeholder="606" value={totalLength} onChange={(event)=>setTotalLength(event.target.value)}/>
                                                </Control>
                                            </Field>
                                        </Box>
                                    </Level.Item>
                                    <Level.Item id="level-shrink"> 
                                        <Box>
                                            <Field>
                                                <Label>Discipline</Label>
                                                <Dropdown onChange={(value)=>{setDiscipline(value)}} value={discipline}>
                                                    <Dropdown.Item value="traditional">
                                                        Traditional
                                                    </Dropdown.Item>
                                                    <Dropdown.Item value="sport">
                                                        Sport
                                                    </Dropdown.Item>    
                                                    <Dropdown.Item value="mixed">
                                                        Mixed
                                                    </Dropdown.Item>
                                                    <Dropdown.Item value="ice">
                                                        Ice
                                                    </Dropdown.Item>
                                                    <Dropdown.Item value="alpine">
                                                        Alpine
                                                    </Dropdown.Item>
                                                </Dropdown>
                                            </Field>
                                        </Box>
                                    </Level.Item>
                                    <Level.Item id="level-shrink">
                                    <Box>
                                        <Field>
                                            <Label>Grade</Label>
                                                <Level>
                                                    <Level.Item>
                                                        <Dropdown onChange={(value)=>{setGradeInfo( new ClimbingGrade(value, gradeInfo._inputSystemName))}} value={gradeInfo._inputGrade}>
                                                            {gradeInfo._systems[gradeInfo._inputSystemName].grades.filter(unique).map((grade) => (
                                                                <Dropdown.Item key={grade} value={grade}>
                                                                    {grade}
                                                                </Dropdown.Item>
                                                            ))}
                                                        </Dropdown>
                                                    </Level.Item>
                                                    <Level.Item>
                                                        <Dropdown onChange={(value)=>{setSeverity(value)}} value={severity}>
                                                            <Dropdown.Item value=''>
                                                                N/A
                                                            </Dropdown.Item>
                                                            <Dropdown.Item value='PG-13'>
                                                                PG-13
                                                            </Dropdown.Item>
                                                            <Dropdown.Item value='R'>
                                                                R
                                                            </Dropdown.Item>
                                                            <Dropdown.Item value='X'>
                                                                X
                                                            </Dropdown.Item>
                                                        </Dropdown>
                                                    </Level.Item>
                                                    <Level.Item>
                                                        <Dropdown onChange={(value)=>{setGradeInfo(new ClimbingGrade(value.placeholderGrade, value.system))}} value={gradeInfo.system}>
                                                            <Dropdown.Item value={{placeholderGrade: '5.10a', system: 'yds'}}>
                                                                Yosemite Decimal System
                                                            </Dropdown.Item>
                                                            <Dropdown.Item value={{placeholderGrade: '6a', system: 'french'}}>
                                                                French
                                                            </Dropdown.Item>    
                                                            <Dropdown.Item value={{placeholderGrade: '18', system: 'australian'}}>
                                                                Australian
                                                            </Dropdown.Item>
                                                            <Dropdown.Item value={{placeholderGrade: 'hvs 5a', system: 'british'}}>
                                                                British
                                                            </Dropdown.Item>
                                                            <Dropdown.Item value={{placeholderGrade: '19', system: 'south_african'}}>
                                                                South African
                                                            </Dropdown.Item>
                                                            <Dropdown.Item value={{placeholderGrade: 'VB', system: 'hueco'}}>
                                                                Hueco
                                                            </Dropdown.Item>
                                                            <Dropdown.Item value={{placeholderGrade: '3+', system: 'font'}}>
                                                                Fontainebleau
                                                            </Dropdown.Item>  
                                                        </Dropdown>
                                                    </Level.Item>
                                                </Level>
                                            </Field>
                                        </Box>
                                    </Level.Item>
                                </Level.Side>
                            </Level>

                            <Box>
                                <Columns>
                                    <Columns.Column size="half">
                                            <Field>
                                                <Label>Gear</Label>
                                                <Control>
                                                    <Textarea placeholder="Standard Rack to 3 in." value={gear} onChange={(event)=> setGear(event.target.value)}/>
                                                </Control>
                                            </Field>
                                    </Columns.Column>
                                    <Columns.Column size="half">
                                            <Field>
                                                <Label>Anchors</Label>
                                                <Control>
                                                    <Textarea placeholder="Bolted, gear, trees..." value={anchors} onChange={(event)=> setAnchors(event.target.value)}/>
                                                </Control>
                                            </Field>
                                    </Columns.Column>
                                </Columns>
                            </Box>

                            <Box>
                                <Level>
                                    <Level.Side align="left">
                                        <Level.Item>
                                            <Field>
                                                <Label>Elevation</Label>
                                                <Level>
                                                    <Level.Side align="left">
                                                        <Level.Item>
                                                            <Control>
                                                                <Input placeholder={"2000"} value={elevation} onChange={(event)=> setElevation(event.target.value)}/>
                                                            </Control>
                                                        </Level.Item>
                                                        <Level.Item>
                                                            <Field kind="addons">
                                                                <Control>
                                                                    <Button
                                                                        color={unitState === 'm' ? "primary" : ""}
                                                                        onClick={() => setUnitState('m')}>
                                                                        Metres
                                                                    </Button>
                                                                </Control>
                                                                <Control>
                                                                    <Button
                                                                        color={unitState === 'ft' ? "primary" : ""}
                                                                        onClick={() => setUnitState('ft')}>
                                                                        Feet
                                                                    </Button>
                                                                </Control>
                                                            </Field>
                                                        </Level.Item>
                                                    </Level.Side>
                                                </Level>
                                            </Field>
                                        </Level.Item>
                                    </Level.Side>
                                    <Level.Side align="right">
                                        <Level.Item>
                                            <Field>
                                                <Label>Aspect</Label>
                                                <Dropdown onChange={(value)=>{setAspect(value)}} value={aspect}>
                                                            <Dropdown.Item value={"NW"}>
                                                                NW
                                                            </Dropdown.Item>
                                                            <Dropdown.Item value={"N"}>
                                                                N
                                                            </Dropdown.Item>    
                                                            <Dropdown.Item value={"NE"}>
                                                                NE
                                                            </Dropdown.Item>
                                                            <Dropdown.Item value={"E"}>
                                                                E
                                                            </Dropdown.Item>
                                                            <Dropdown.Item value={"SE"}>
                                                                SE
                                                            </Dropdown.Item>
                                                            <Dropdown.Item value={"S"}>
                                                                S
                                                            </Dropdown.Item>
                                                            <Dropdown.Item value={"SW"}>
                                                                SW
                                                            </Dropdown.Item>
                                                            <Dropdown.Item value={"W"}>
                                                                W
                                                            </Dropdown.Item>
                                                </Dropdown>
                                            </Field>
                                        </Level.Item>
                                    </Level.Side>
                                </Level>
                            </Box>

                            <Box>
                                <Columns>
                                    <Columns.Column size="one-third">
                                        <Field>
                                            <Label>Approach</Label>
                                            <Control>
                                                <Textarea placeholder="Parking, hike in..." value={approach} onChange={(event)=> setApproach(event.target.value)}/>
                                            </Control>
                                        </Field>
                                    </Columns.Column>
                                    <Columns.Column size="one-third">
                                        <Field>
                                            <Label>Descent</Label>
                                            <Control>
                                                <Textarea placeholder="Walk-off, rappel..." value={descent} onChange={(event)=> setDescent(event.target.value)}/>
                                            </Control>
                                        </Field>
                                    </Columns.Column>
                                    <Columns.Column size="one-third">
                                        <Field>
                                            <Label>Route Conditions</Label>
                                            <Control>
                                                <Textarea placeholder="Loose rock, early season conditions, wet..." value={conditions} onChange={(event)=>setConditions(event.target.value)}/>
                                            </Control>
                                        </Field>
                                    </Columns.Column>
                                </Columns>
                            </Box>

                        </Box>
                        {errorMessage}
                        <Level>
                            <Level.Side align='right'>
                                <Level.Item>
                                    <Field>
                                        <Button color='primary' onClick={()=>handleUpdate()}>Update</Button>
                                    </Field>
                                </Level.Item>
                                <Level.Item>
                                    <Field>
                                        <Link to="/tom-novak/home">
                                            <Button color='danger'>Discard</Button>
                                        </Link>
                                    </Field>
                                </Level.Item>
                            </Level.Side>
                        </Level>

                    </Columns.Column>
                    <Columns.Column/>
                </Columns>
        </Box>
    );
}

export default withRouter(EditPitchReportPage);