import React, { createRef } from 'react';
import { Box, Table } from 'react-bulma-components';
import {
    sortReports,
    totalElevation,
    totalLength,
    totalTime,
    totalPitches } from './helpers/reportOperators.js';
import AnimateChildren from './AnimateChildren.js';


const AnalyticsTable = ({ userInfo, sortBy='date' }) => {
    //Initialize report array
    const reports = userInfo.pitchReports;

    const sortedReports = sortReports(userInfo.pitchReports, sortBy);

    const tableBody = sortedReports.map((report) => (
        <tr key={report.title} ref={createRef()}>
            <td>{report.title}</td>
            <td>{report.route}</td>
            <td>{report.grade + " " + report.sev}</td>
            <td>{report.pitches}</td>
            <td>{report.totalLength}</td>
            <td>{report.totalTime}</td>
            <td>{report.elev}</td>
            <td>{report.date.substring(0, 10)}</td>
        </tr>
    ));

    //Render
    return (
        <Box>
            <Table>
                <thead>
                    <tr>
                        <th>Name</th>
                        <th>Route</th>
                        <th>Grade</th>
                        <th>Pitches</th>
                        <th>Length (m)</th>
                        <th>Time on Route (hrs)</th>
                        <th>Elevation (m)</th>
                        <th>Date</th>
                    </tr>
                </thead>
                <tfoot>
                    <tr>
                        <th>TOTALS</th>
                        <th></th>
                        <th></th>
                        <th>{totalPitches(reports)}</th>
                        <th>{totalLength(reports)}</th>
                        <th>{totalTime(reports)}</th>
                        <th>{totalElevation(reports)}</th>
                        <th></th>
                    </tr>
                </tfoot>
                <tbody>
                    <AnimateChildren>
                        {tableBody}
                    </AnimateChildren>
                </tbody>
            </Table>
        </Box>
    );
}

export default AnalyticsTable;