import React, { useState } from 'react';
import { Link } from 'react-router-dom';
import { formatTripID } from './helpers/formatParser.js';
import {
    Box,
    Button,
    Columns,
    Heading,
    Image,
    Level,
    List
} from 'react-bulma-components';
import NamedField from '../components/NamedField.js';
import NamedFieldLevel from '../components/NamedFieldLevel.js';
import { getS3Image } from '../components/helpers/s3Interface.js';
import './css/logListEntry.css';

const LogListEntry = ({ user, report, setUserInfo }) => {
    //Declarations
    const maxDescriptionLength = 688;
    const description = report.desc && report.desc.length >= maxDescriptionLength ?
        report.desc.substring(0,maxDescriptionLength) + " . . ." : report.desc;

    const [firstLoad, setFirstLoad] = useState(true);
    const [coverImg, setCoverImg] = useState(null);
    const [coverImgPlaceholder, setCoverImgPlaceholder] = useState(null);

    if(firstLoad) {
        getS3Image('mountain-placeholder.jpg').then(img => setCoverImgPlaceholder(img));
        if(report.coverImg) {
            getS3Image(report.coverImg).then(img => {setCoverImg(img)});
        }
        setFirstLoad(false);
    }

    //Event Handlers
    const handleUpvoteReport = async () => {
        const result = await fetch(`/api/${user}/picture-trip-reports/${report.id}/upvote`, {
            method: 'post',
        });
        const body = await result.json();
        setUserInfo(body);
    }

    //Conditional Renders
    const coverImgSrc = report.coverImg ? coverImg : coverImgPlaceholder;

    //Render
    return (
        <>
        <List.Item>
            <Columns className="has-text-left">
                <Columns.Column>
                    <Box>
                        <img id='log-img' alt={coverImgSrc} src={coverImgSrc}/>
                    </Box>
                </Columns.Column>
                <Columns.Column size="half">
                    <Level>
                        <Level.Side align="left">
                            <Level.Item>
                                <div>
                                    <Link to={`/${user}/photo-report/${formatTripID(report.title)}`}>
                                        <Heading className="has-text-weight-bold">{report.title}</Heading>
                                    </Link>
                                    <Heading subtitle>{report.date.substring(0, report.date.search('T'))}</Heading>
                                </div>
                            </Level.Item>
                        </Level.Side>
                        <Level.Side align="right">
                            <Level.Item>
                                <Box><Heading subtitle>Upvotes: <strong>{report.upvotes}</strong></Heading></Box>
                            </Level.Item>
                            <Level.Item>
                                <Button onClick={() => handleUpvoteReport()} color="white"><Image size={32} src="http://localhost:3000/images/upvote-icon.png"/></Button>
                            </Level.Item>
                        </Level.Side>
                    </Level>
                    <p style={{"whiteSpace": "pre-line"}}>{description}</p>
                </Columns.Column>
                <Columns.Column>
                    <Box>
                        <NamedFieldLevel name="Route" content={report.route} />
                        <NamedFieldLevel name="Location" content={report.loc} />
                        <Level>
                            <Level.Side align="left">
                                <Level.Item>
                                    <Heading heading className="has-text-weight-bold">Coordinates</Heading>
                                </Level.Item>
                            </Level.Side>
                            <Level.Side align="right">
                                <Level.Item>
                                    <Heading heading>{report.coord[0]}, {report.coord[1]}</Heading>
                                </Level.Item>
                            </Level.Side>
                        </Level>

                        <Level id="centered-level-item">
                            <Level.Item>
                                <NamedField name="Discipline" content={report.disc} />
                            </Level.Item>
                            <Level.Item>
                                <NamedField name="Grade" content={report.grade ? report.grade + " " + report.sev : "\u2014"} />
                            </Level.Item>
                            <Level.Item>
                                <NamedField name="Pitches" content={report.pitches} />
                            </Level.Item>
                        </Level>

                        <Level id="centered-level-item">
                            <Level.Item>
                                <NamedField name="Total Length (m)" content={report.totalLength} />
                            </Level.Item>
                            <Level.Item>
                                <NamedField name="Time (hrs)" content={report.totalTime} />
                            </Level.Item>
                            <Level.Item>
                                <NamedField name="Elevation (m)" content={report.elev} />
                            </Level.Item>
                        </Level>
                    </Box>
                </Columns.Column>
            </Columns>
        </List.Item>
        </>
    );
}

export default LogListEntry;