import React, { useState, useEffect, createRef } from 'react';
import {
    Columns,
    Message,
} from 'react-bulma-components';
import Scroll from 'react-scroll';
import FlyoutDetailsColumn from '../components/FlyoutDetailsColumn.js'
import RouteProgressBar from '../components/RouteProgressBar.js';
import { Carousel } from 'react-responsive-carousel';
import '../../node_modules/react-responsive-carousel/lib/styles/carousel.css';
import './css/pictureReportPage.css';
import { getS3Image } from '../components/helpers/s3Interface.js';

const scrollToContent = () => {
    let scroll = Scroll.animateScroll;
    scroll.scrollTo(57);
}

const formatPitchNum = (number) => {
    if( !isNaN(number) ){
        return `Pitch ${number}`;
    } else {
        return number;
    }
}

const PictureReportPage = ( {match} ) => {
    //Declarations
    const username = match.params.user;
    const reportID = match.params.reportID;
    const [currentSlide, setCurrentSlide] = useState(0);
    const [reportInfo, setReportInfo] = useState(null);
    const [fullScreen, setFullScreen] = useState(false);
    const [imageHeight, setImageHeight] = useState("72vh");
    const [imageColumnWidth, setImageColumnWidth] = useState('is-7');
    const [detailColumnWidth, setDetailColumnWidth] = useState('is-4');

    const [imgRefs, setImgRefs] = useState([]);

    const [images, setImages] = useState([]);

    const [segmentArray, setSegmentArray] = useState([]);

    const [firstLoad, setFirstLoad] = useState(true);

    //prepare image array
    if(reportInfo && reportInfo.pitchArray.length > 0 && images.length === 0) {
        let workingImgArray = [];
        let workingSegmentArray = isNaN(reportInfo.pitches) ? [] : new Array( parseInt(reportInfo.pitches, 10) ).fill(null);

        if ( reportInfo.coverImg ) {
            workingImgArray = workingImgArray.concat({
                image: reportInfo.coverImg,
                caption: reportInfo.desc,
                grade: null,
                length: null,
                number: 'Summary'
            });
        }

        reportInfo.pitchArray.map((pitch) => {
            if (pitch) {
                if( !isNaN(pitch.number) ) {
                    workingSegmentArray[pitch.number] = {
                        pitch: pitch.number,
                        grade: pitch.grade._inputGrade,
                        length: pitch.length
                    }
                }
                pitch.images.map((image, index) => {

                    workingImgArray = workingImgArray.concat({
                        image: image,
                        caption: pitch.captions[index],
                        grade: pitch.grade,
                        length: pitch.length,
                        number: pitch.number
                    });
                    return 0;
                });
            }
            return 0;
        });
        setImages(workingImgArray);
        if( isNaN(reportInfo.pitches) ) {
            setSegmentArray([]);
        } else {
            setSegmentArray(workingSegmentArray);
        }
    }

    //set image refs once on loading
    if(images.length > 0 && imgRefs.length === 0) {
        let refArray = new Array(images.length);
        let i = 0;

        for(i = 0; i < images.length; i++) {
            refArray[i] = createRef();
        }
        setImgRefs(refArray);
    }

    if( reportInfo && firstLoad && images.length > 0 ) {

        const workingImages = [...images];

        images.map((image, index) => { 
            getS3Image(image.image).then(img => {
                workingImages[index].image = img;
                if(index === images.length - 1) {
                    setImages(workingImages);
                }
            });
            return 0;
        })

        setFirstLoad(false);
    }

    //scroll down to content on loading
    if(!reportInfo) {
        scrollToContent();
    }

    //Event Handlers
    useEffect(() => {
        const fetchData = async () => {
            const result = await fetch(`/${username}/picture-trip-reports/${reportID}`);
            const body = await result.json();
            setReportInfo(body);
        }
        fetchData();
    }, [username, reportID]);

    const updateCurrentSlide = (index) => {
        if (currentSlide !== index) {
            setCurrentSlide(index);
        }
    }

    const resize = () => {

        let workingFullscreenState = !fullScreen;
        setFullScreen(!fullScreen);

        if( imgRefs[currentSlide].current && workingFullscreenState){
            //smol algebra break
            let heightWidthRatio = imgRefs[currentSlide].current.naturalHeight/imgRefs[currentSlide].current.naturalWidth;
            let scaledHeight = (window.innerWidth * (10/12) ) * heightWidthRatio;
            
            setImageHeight(`${scaledHeight}px`);
            setImageColumnWidth('is-10');
            setDetailColumnWidth(null);
        } else {
            setImageHeight('72vh');
            setImageColumnWidth('is-7');
            setDetailColumnWidth('is-4');

            scrollToContent();
        }
    };

    const routeDetail = reportInfo ? 
    <FlyoutDetailsColumn 
        user={username}
        reportInfo={reportInfo} 
        show={!fullScreen} 
        columnWidth={detailColumnWidth}
        height={imageHeight} /> : null;

        
    //Conditional Renders 
    const imageCarousel = images.length > 0 ? images.map((image, key) => {
        return(
        <div
            key={image}
            style={{
                height: imageHeight,
                transition: 'height .3s ease-out'
            }}
            >
            {/* gross code below - could be done much better if this were to be done again */}
            { !firstLoad && (<img
                alt={image.image}
                src={image.image}
                ref={imgRefs[key]}/> )}
        </div>
        );
    }) : null;

    const progressBar = segmentArray.length > 0 && images[currentSlide] ? <RouteProgressBar segmentArray={segmentArray} filledIndex={images[currentSlide].number}/> : null;
    return(
        <Columns className='has-background-dark' id='picture-report-content'>
            <Columns.Column className='is-1'>
                {progressBar}
            </Columns.Column>
            <Columns.Column id='picture-report-column' className={imageColumnWidth}>
                <Carousel
                    showThumbs={false}
                    onChange={updateCurrentSlide}
                    onClickItem={()=>resize()}
                    selectedItem={currentSlide}>
                    {imageCarousel}
                </Carousel>
                    <div ref={createRef()}>
                        <Carousel
                            showThumbs={false}
                            showStatus={false}
                            showIndicators={false}
                            onChange={updateCurrentSlide}
                            selectedItem={currentSlide}>
                            {
                                images.length > 0 ? images.map((image, key) => (
                                    <div key={key}>
                                        <Message>
                                            <Message.Header>
                                                {formatPitchNum(image.number)}
                                            </Message.Header>
                                            <Message.Body  style={{height: '18vh', 'overflowY': 'scroll', 'scrollbarWidth': 'none'}}>
                                                <p align='left' heading='true' className='is-size-6'>{image.caption}</p>
                                            </Message.Body>
                                        </Message>
                                    </div>
                                )) : null
                            }
                        </Carousel>
                    </div>
            </Columns.Column>
            
            {routeDetail}

        </Columns>
    );
}

export default PictureReportPage;