import { useControlled } from "@material-ui/core";
import { Image } from 'react-bulma-components';

const { CognitoIdentityClient } = require("@aws-sdk/client-cognito-identity");
const {
  fromCognitoIdentityPool,
} = require("@aws-sdk/credential-provider-cognito-identity");
const { S3Client, GetObjectCommand, PutObjectCommand } = require("@aws-sdk/client-s3");

// Initialize the Amazon Cognito credentials provider
const REGION = "us-west-2"; //e.g., 'us-east-1'
const s3 = new S3Client({
region: REGION,   
credentials: fromCognitoIdentityPool({
    client: new CognitoIdentityClient({region:  REGION }),
    identityPoolId: "us-west-2:ad443065-eddd-405b-bfa1-9a25eeae2bc0", // IDENTITY_POOL_ID e.g., eu-west-1:xxxxxx-xxxx-xxxx-xxxx-xxxxxxxxx
}),
});

// List the photo albums that exist in the bucket
var BucketName = "t-novak-trips"; //BUCKET_NAME

const s3Img = (imgSrc, size=null, style=null, ref=null) => {
    return typeof imgSrc === 'string' ? <Image size={size} style={style} src={imgSrc}/> : null;
}

const getS3Image = async (imageName) => {
    try {
        return s3.send(new GetObjectCommand({Bucket: BucketName, Key: `images/${imageName}`}))
        .then(response => {
            const reader = response.Body.getReader();
            return new ReadableStream({
                start(controller) {
                    return pump();
                    function pump() {
                        return reader.read().then(({ done, value }) => {
                            if(done) {
                                controller.close();
                                return;
                            }
                            controller.enqueue(value);
                            return pump();
                        });
                    }
                }
            })
        })
        .then(stream => new Response(stream))
        .then(response => response.blob())
        .then(blob => URL.createObjectURL(blob))
        .then(url => {return url;})
    } catch(err) {
        console.error(err);
    }
}

const uploadS3Image = async (imgFile) => {
    const file = imgFile[0];
    const fileName = file.name;
    const photoKey = `images/${fileName}`;
    const uploadParams = {
        Bucket: BucketName,
        Key: photoKey,
        Body: file,
        ACL: "public-read",
    };
    try {
        return s3.send(new PutObjectCommand(uploadParams));
    } catch (err) {
        console.error(err);
    }
}

export { getS3Image, uploadS3Image, s3Img };