import React from 'react';
import {
  BrowserRouter as Router,
  Route,
  Redirect
} from 'react-router-dom';
import AboutPage from './pages/AboutPage';
import AddReportPage from './pages/AddReportPage';
import AddPitchReportPage from './pages/AddPitchReportPage';
import EditReportPage  from './pages/EditReportPage';
import EditPitchReportPage from './pages/EditPitchReportPage';
import HomePage from './pages/HomePage';
import ReportPage from './pages/ReportPage';
import ReportsFeedPage from './pages/ReportsFeedPage';
import ScrollToTop from './components/ScrollToTop';
import TripNavBar from './components/TripNavBar';
import TripFooter from './components/TripFooter';
import './App.css';
import PictureReportPage from './pages/PictureReportPage';

function App() {

  return (
    <Router>
      <ScrollToTop>
        <div className="App">
          <TripNavBar />
          <div id="page-body">
            <Route exact path="/">
              {/* temporary redirect to tom-novak home page. Will be reworked when multiple users added */}
              <Redirect to="/tom-novak/home" />
            </Route>
            <Route path="/:user/home" component={HomePage} exact />
            <Route path="/:user/about" component={AboutPage} />
            <Route path="/:user/trip-report-feed/:view" exact component={ReportsFeedPage} />
            <Route path="/:user/trip-reports/:reportID" component={ReportPage} />
            <Route path="/:user/add-report" component={AddReportPage} />
            <Route path="/:user/add-pitch-report" component={AddPitchReportPage} />
            <Route path="/:user/photo-report/:reportID" component={PictureReportPage} />
            <Route path="/:user/edit-report/:reportID" component={EditReportPage} />
            <Route path="/:user/edit-photo-report/:reportID" component = {EditPitchReportPage} />
          </div>
          <TripFooter/>
        </div>
      </ScrollToTop>
    </Router>
  );
}

export default App;
