import React, { useState } from 'react';
import {
    Box,
    Button,
    Dropdown,
    Form,
    Heading,
    Icon,
    Level,
} from 'react-bulma-components';
import { Carousel } from 'react-responsive-carousel';
import ClimbingGrade from 'climbing-grade';
import ClimbingPitch from '../components/helpers/ClimbingPitch.js'
import { getS3Image, uploadS3Image } from '../components/helpers/s3Interface';

const { Input, Label, Textarea, InputFile } = Form;

const unique = (value, index, self) => {
    return self.indexOf(value) === index
  }

const AddPitchBox = ({pitch, setParent, index, gradeSystem, deletePitch}) => {

    const [currentSlide, setCurrentSlide] = useState(0);

    const workingPitch = new ClimbingPitch(pitch.number, pitch.grade, pitch.length, pitch.images, pitch.captions);

    const [images, setImages] = useState(pitch.images);
 
    const title = isNaN(pitch.number) ? pitch.number : `Pitch ${pitch.number}` ;

    const gradeContent = pitch.grade ? 
            <Dropdown onChange={(value)=>handleGrade(value)} value={pitch.grade._inputGrade}>
                {pitch.grade._systems[gradeSystem].grades.filter(unique).map((grade) => (
                    <Dropdown.Item key={grade} value={grade}>
                        {grade}
                    </Dropdown.Item>
                ))}
            </Dropdown> : null;

    const handleGrade = (value) => {
        workingPitch.grade=(new ClimbingGrade(value, pitch.grade._inputSystemName));
        setParent(workingPitch, index);
    }

    const handleLength = (value) => {
        if( !isNaN(value) ) {
            workingPitch.length=value;
            setParent(workingPitch, index);
        }
    }

    const handleImage = (value) => {
        workingPitch.images = workingPitch.images.concat([value[0].name]);
        images.concat([value[0].name]);
        uploadS3Image(value)
        .then(response => images.map((image, index) => {
            if( !image.startsWith('blob') ) {
                getS3Image(image).then(img => {
                    console.log('hit');
                    let tempImages = [...images];
                    tempImages[index] = img;
                    setImages(tempImages);
                });
            }
            return 0;
        }));
        workingPitch.captions = workingPitch.captions.concat('');
        setParent(workingPitch, index);
    }

    const handleCaptionChange = (value, key) => {
        let newCaptions = [...workingPitch.captions];
        newCaptions[key] = value;
        workingPitch.captions = newCaptions;
        setParent(workingPitch, index);
    }

    const updateCurrentSlide = (index) => {
        if (currentSlide !== index) {
            setCurrentSlide(index);
        }
    }

    const handleDelete = () => {
        deletePitch(index);
    }

    const handleDescent = () => {
        if( !isNaN(workingPitch.number) ) {
            workingPitch.number = 'Descent';
        } else {
            workingPitch.number = index;
        }
        setParent(workingPitch, index);
    }

    // workingPitch.images.map((image, index) => {
    //     if(!image.startsWith('blob')) {
    //         getS3Image(image).then(img => {
    //             workingPitch.images[index] = img;
    //         });
    //     }
    // })

    const imageCarousel = workingPitch.images.length > 0 ? workingPitch.images.map((image, key) => {
        if( image.startsWith('blob') ) {
            return(
                <img
                    key={key}
                    alt={image}
                    src={image}/>
            )
        } else {
            return null;
        }
    }) : null;

    const captionContent = workingPitch.captions.length > 0 ? workingPitch.captions.map((caption, key) => (
        <div key={key}>
            <Textarea
                placeholder='Caption'
                value={pitch.captions[key]}
                onChange={(event)=>handleCaptionChange(event.target.value, key)}/>
        </div>
    )) : null;

    return(
        <Box>
            <Level>
                <Level.Side align='left'>
                    <Level.Item>
                        <Label>{title}</Label>
                    </Level.Item>
                </Level.Side>
                <Level.Side align='right'>
                    <Level.Item>
                        <Heading heading>Grade:</Heading>
                    </Level.Item>
                    <Level.Item>
                        {gradeContent}
                    </Level.Item>
                    <Level.Item>
                        <Heading heading>Length (m):</Heading>
                    </Level.Item>
                    <Level.Item>
                        <Input
                            placeholder="30"
                            value={pitch.length} 
                            onChange={(event)=> handleLength(event.target.value)}/>
                    </Level.Item>
                    <Level.Item>
                        <Button onClick={handleDelete} color='light'>Remove Pitch</Button>
                    </Level.Item>
                    <Level.Item>
                        <Button onClick={handleDescent} color={workingPitch.number === 'Descent' ? "primary" : "light"}>Descent</Button>
                    </Level.Item>
                </Level.Side>
            </Level>
            <Heading heading>Images</Heading>
            <Carousel
                showThumbs={false}
                onChange={updateCurrentSlide}
                selectedItem={currentSlide}>
                {imageCarousel}
            </Carousel>
            <Carousel
                showThumbs={false}
                showStatus={false}
                showIndicators={false}
                onChange={updateCurrentSlide}
                selectedItem={currentSlide}>
                {captionContent}
            </Carousel>

            <Level>
                <Level.Item>
                    <InputFile
                        icon={<Icon icon='upload' />}
                        boxed
                        onChange={(event)=>handleImage(event.target.files)} />
                </Level.Item>
            </Level>
        </Box>
    );
}

export default AddPitchBox;