import ClimbingGrade from 'climbing-grade';

function sortReports(reports, sortBy='date') {
    let sortedReports = null;

    switch(sortBy) {
        case 'pitch':
            sortedReports = reports.sort(sortByPitches);
            break;
        case 'grade':
            sortedReports = reports.sort(sortByGrade);
            break;
        case 'elevation':
            sortedReports = reports.sort(sortByElev);
            break;
        case 'length':
            sortedReports = reports.sort(sortByLength);
            break;
        case 'time':
            sortedReports = reports.sort(sortByTime);
            break;
        default:
            sortedReports = reports.sort(sortByDate);
            break;
    }

    return sortedReports;
}

const sortByDate = (a, b) => {
    let dateA = new Date(a.date);
    let dateB = new Date(b.date);

    if( dateA.getTime() === dateB.getTime() ) {
        return 0;
    } else if (dateA > dateB ) {
        return -1;
    } else {
        return 1;
    }
}

const sortByPitches = (a, b) => {
    let pitchesA = numberCheck(Number(a.pitches), 0);
    let pitchesB = numberCheck(Number(b.pitches), 0);

    if( pitchesA === pitchesB ) {
        return 0;
    } else if ( pitchesA > pitchesB) {
        return -1;
    } else {
        return 1;
    }
}

const sortByGrade = (a, b) => {
    let gradeA = new ClimbingGrade( a.grade, 'yds');
    let gradeB = new ClimbingGrade( b.grade, 'yds');

    let universalGradeA = gradeA._universalGrades[0];
    let universalGradeB = gradeB._universalGrades[0];

    if( universalGradeA === universalGradeB ) {
        return 0;
    } else if ( universalGradeA > universalGradeB ) {
        return -1;
    } else {
        return 1;
    }
}

const sortByElev = (a, b) => {
    let elevA = Number(a.elev);
    let elevB = Number(b.elev);

    if( elevA === elevB ) {
        return 0;
    } else if ( elevA > elevB ) {
        return -1;
    } else {
        return 1;
    }
}

const sortByLength = (a, b) => {
    let lengthA = numberCheck(Number(a.totalLength), 0);
    let lengthB = numberCheck(Number(b.totalLength), 0);
    
    if( lengthA === lengthB ) {
        return 0;
    } else if ( lengthA > lengthB ) {
        return -1;
    } else {
        return 1;
    }
}

const sortByTime = (a, b) => {
    let timeA = numberCheck(Number(a.totalTime), Number.MAX_SAFE_INTEGER);
    let timeB = numberCheck(Number(b.totalTime), Number.MAX_SAFE_INTEGER);

    if( timeA === timeB ) {
        return 0;
    } else if ( timeA > timeB ) {
        return 1;
    } else {
        return -1;
    }
}

const numberCheck = (input, replacement) => {
    if(isNaN(input)) {
        return replacement;
    } else {
        return input;
    }
}

const totalPitches = (reports) => {
    let sum = 0;
    reports.forEach((report) => {
        if(!isNaN(Number(report.pitches))) {
            sum += Number(report.pitches);
        }
    })
    return sum;
}

const totalTime = (reports) => {
    let sum = 0;
    reports.forEach((report) => {
        if(!isNaN(Number(report.totalTime))) {
            sum += Number(report.totalTime);
        }
    })
    return sum;
}

const totalLength = (reports) => {
    let sum = 0;
    reports.forEach((report) => {
        if(!isNaN(Number(report.totalLength))) {
            sum += Number(report.totalLength);
        }
    })
    return sum;
}

const totalElevation = (reports) => {
    let sum = 0;
    reports.forEach((report) => {
        if(!isNaN(Number(report.elev))) {
            sum += Number(report.elev);
        }
    })
    return sum;
}

export { sortReports, totalPitches, totalTime, totalLength, totalElevation };