import React from 'react';
import Tooltip from '@material-ui/core/Tooltip'

import './css/routeProgressBar.css';

const Filler = ({ filled }) => {
    const percentage = filled ? 100 : 0;

    return <div 
            className="filler"
            style={{ height: `${percentage}%`}} />
}

const HoverInfo = ({ segment }) => {
    return(
        <>
            <p className="is-size-7">{`Pitch ${segment.pitch}`}</p>
            <p className="is-size-7">{segment.grade}</p>
            <p className="is-size-7">{`${segment.length} m`}</p>
        </>
    );
}

const processFilledIndex = (segmentArray, filledIndex) => {
    if ( !isNaN(filledIndex) ) {
        return(filledIndex);
    } else if (filledIndex === 'Descent') {
        return(segmentArray.length);
    } else {
        return(0);
    }
}

const Segment = ({ className, segment, height, filled, toolTip }) => {
    if(segment && segment.pitch && segment.grade && segment.length ) {
        return(
            <div>
                <Tooltip
                    title={<HoverInfo segment={segment}/>}
                    placement='right'
                    arrow
                    open={toolTip}>
                    <div 
                        style={{height: `${height}px`}} 
                        className={className}>
                        <Filler filled={filled}/>
                    </div>
                </Tooltip>
            </div>
        );
    } else {
        return(
            <div 
                style={{height: `${height}px`}} 
                className={className}>
                <Filler filled={filled}/>
            </div>
        );
    }
}

const RouteProgressBar = ({ segmentArray, filledIndex }) => {
    const segmentHeight = Math.round((window.innerHeight - 122)/segmentArray.length);
    const workingArray = [...segmentArray].reverse();
    const workingFilledIndex = segmentArray.length - processFilledIndex(segmentArray, filledIndex);
    const descentColor = (workingFilledIndex === 0) ? '#1DA598' : 'white';
    return (
        <>
        {/* in progress  */}
        <p
        heading='true'
        id='descent-segment'
        className='is-size-4 has-text-weight-bold'
        style={{'margin-top': '15px', 'margin-left': '10px', color: descentColor}}>Descent</p>
        {
            workingArray.map((segment, index) => {

                if (index === 0) {
                    return(
                        <Segment
                            className="route-progress-end"
                            height={segmentHeight}
                            segment={segment}
                            filled={index >= (workingFilledIndex - 1)}
                            toolTip={index === (workingFilledIndex - 1)}
                            key={`prog-bar-segment-${index}`} />
                    );
                } else if (index === (segmentArray.length - 2)) {
                    return(
                        <Segment
                            className="route-progress-start"
                            height={segmentHeight}
                            segment={segment}
                            filled={index >= (workingFilledIndex - 1)}
                            toolTip={index === (workingFilledIndex - 1)}
                            key={`prog-bar-segment-${index}`} />
                    );
                } else if (index === (segmentArray.length - 1)) {
                    return null;
                }
                else {
                    return(
                        <Segment
                            className="route-progress-middle"
                            height={segmentHeight}
                            segment={segment} 
                            filled={index >= (workingFilledIndex - 1)}
                            toolTip={index === (workingFilledIndex - 1)}
                            key={`prog-bar-segment-${index}`} />
                    );
                }
            })
        }

        <p
        heading='true'
        id='approach-segment'
        color='primary'
        className='is-size-4 has-text-weight-bold'>Approach</p>
        </>
    );
}

export default RouteProgressBar;