import React, { useState } from 'react';
import 'react-bulma-components/dist/react-bulma-components.min.css'
import { Navbar, Button } from 'react-bulma-components';
import { Link, withRouter } from 'react-router-dom';
import logo from '../images/mountain-logo.jpg'


const TripNavBar = (props) => {
    //Declarations
    const [isActive, setIsActive] = useState(false);

    //Render
    return (
        <Navbar>
            <Navbar.Brand>
                <Navbar.Item renderAs="a" href="#">
                    <img src={logo} alt="logo" width="112" />
                </Navbar.Item>
            <Navbar.Burger onClick={() => setIsActive(!isActive)}>
                <span/>
                <span/>
                <span/>
            </Navbar.Burger>
            </Navbar.Brand>
            <Navbar.Menu>
                <Navbar.Container>
                    <Link to="/tom-novak/home" className="navbar-item">Home</Link>
                    <Navbar.Item dropdown hoverable>
                        <Navbar.Link arrowless={true}>
                            <Link to="/tom-novak/trip-report-feed/list" className="navbar-item">Trip Reports</Link>
                        </Navbar.Link>
                        {/* <Navbar.Dropdown>
                            <Link to="/tom-novak/trip-report-feed/list" className="navbar-item">List View</Link>
                            <Link to="/tom-novak/trip-report-feed/maps" className="navbar-item">Map View</Link>                       
                            <Link to="/tom-novak/trip-report-feed/table" className="navbar-item">Table View</Link>
                        </Navbar.Dropdown> */}
                    </Navbar.Item>
                    {/* <Link to="/tom-novak/about" className="navbar-item">About</Link> */}
                </Navbar.Container>
                <Navbar.Container position='end'>
                    <Navbar.Item>
                       <Button onClick={()=>props.history.push("/tom-novak/add-pitch-report")} color='primary'>Add Report</Button>
                    </Navbar.Item>
                </Navbar.Container>
            </Navbar.Menu>
        </Navbar>
    );
}

export default withRouter(TripNavBar);