function formatTripID(tripTitle) {
    return tripTitle.replace(/\s/g, '-').toLowerCase();
}

function formatUserID(user) {
    return user.replace(/\s/g, '-').toLowerCase();
}

function undoFormatTripID(tripID) {
    return tripID.split('-').map(word => word.charAt(0).toUpperCase() + word.substring(1)).join(' ');
}

function undoFormatUserID(userID) {
    return userID.split('-').map(word => word.charAt(0).toUpperCase() + word.substring(1)).join(' ');
}

export { formatTripID, formatUserID, undoFormatTripID, undoFormatUserID, };