import ClimbingGrade from 'climbing-grade';

class ClimbingPitch {
    constructor(number, grade=new ClimbingGrade('5.10a', 'yds'), length='', images=[], captions=[]) {
        
        
        this.number = number === 0 ? 'Approach' : number;
        this.grade = grade;
        this.length = length;
        this.images = images;
        this.captions = captions;
    }
}

export default ClimbingPitch;