import React, { useState, useEffect } from 'react';
import {
    Box,
    Button,
    Columns,
    Container,
    Dropdown,
    Heading,
    Hero,
    Image,
    Level,
    Message,
    Modal,
} from 'react-bulma-components';
import {
    Map,
    Marker,
    TileLayer
} from 'react-leaflet';
import { Carousel } from 'react-responsive-carousel';
import ClimbingGrade from 'climbing-grade';
import NamedField from '../components/NamedField.js';
import NamedFieldLevel from '../components/NamedFieldLevel.js';
import NamedParagraph from '../components/NamedParagraph.js';
import "./css/reportPage.css"
import { undoFormatTripID } from '../components/helpers/formatParser.js';
import { contentCheck } from '../components/contentCheck.js'


const ReportPage = (props) => {
    //Declarations
    //route parameters
    const username = props.match.params.user;
    const reportID = props.match.params.reportID;
    //report info to be fetched
    const [reportInfo, setReportInfo] = useState(null);
    //repotInfo dependants
    const multipleImages = reportInfo ?  reportInfo.images.length > 1 : false;
    const gradeInfo = reportInfo ? new ClimbingGrade( reportInfo.grade, reportInfo.gradeSystem ) : null;
    //view state
    const [fullscreenState, setFullscreenState] = useState(false);
    const [imageHeight, setImageHeight] = useState("74vh");
    const [ displayGradeSystem, setDisplayGradeSystem ] = useState('yds');
    const [ showMap, setShowMap ] = useState(false);
    const [ deletePressed, setDeletePressed ] = useState(false);

    //Conditional Renders
    const imageCarousel = reportInfo ? 
        (multipleImages ? 
            <Carousel
                infiniteLoop
                dynamicHeight={fullscreenState}
                centerMode
                centerSlidePercentage="65"
                showThumbs={false}
                >
                {reportInfo.images.map((image, key) => (
                    <div key={image} style={{height: imageHeight}}>
                        <img alt="" src={image}/>
                        <p className="legend">{reportInfo.captions[key]}</p>
                    </div>
                ))}
            </Carousel> :
            <Columns>
                <Columns.Column/>
                <Columns.Column size="half">
                    <Carousel
                        dynamicHeight={fullscreenState}
                        onClickItem={()=>setFullscreenState(true)}
                        onChange={()=>setFullscreenState(true)}
                        showThumbs={false}>
                        {reportInfo.images.map((image) => (
                            <div key={image} style={{height: imageHeight}}>
                                <img key={image} alt="" src={image}/>
                                <p key={image} className="legend">Test Legend</p>
                            </div>
                        ))}
                    </Carousel>
                </Columns.Column>
                <Columns.Column/>
            </Columns> ) : null ;

    const deleteMessage = deletePressed ?
        <Message color='danger'>
            <Message.Header>
                Are you sure?
                <Button color='danger' onClick={()=>{
                    handleDeleteReport();
                    props.history.push(`/tom-novak/trip-reports`);
                }}>Delete</Button>
                <Button remove onClick={()=>setDeletePressed(false)}/>
            </Message.Header>
        </Message> : null;

    const content = reportInfo ? 
        <>
        <div 
            onMouseEnter={() =>{
                setFullscreenState(true);
                setImageHeight("")
            }}
            style={{backgroundColor: "black"}}>
            {imageCarousel}
        </div>
        <Hero color="primary" size="small">
            <Hero.Body>
                <Container>
                <Heading size={1} align='left'>
                    {reportInfo.title}
                </Heading>
                <Heading subtitle>
                    <Level>
                        <Level.Side align='left'>
                            <Level.Item>{undoFormatTripID(username)}</Level.Item>
                        </Level.Side>
                        <Level.Side align='right'>
                            <Level.Item>{reportInfo.date.substring(0, reportInfo.date.search('T'))}</Level.Item>
                        </Level.Side>
                    </Level>
                </Heading>
                </Container>
            </Hero.Body>
        </Hero>
        <div>
            <Columns className="has-text-left">
                <Columns.Column size="one-fifth">
                    <Box>
                        <Level>
                            <Level.Item>
                                <Heading subtitle size={4}>Upvotes: <strong>{reportInfo.upvotes}</strong></Heading>
                            </Level.Item>
                            <Level.Item>
                                <Button onClick={()=>handleUpvoteReport()} color="white"><Image size={32} src="http://localhost:3000/images/upvote-icon.png"/></Button>
                            </Level.Item>
                        </Level>
                    </Box>
                    <Box>
                        <Level>
                            <Level.Item>
                                <Button onClick={()=>props.history.push(`/tom-novak/edit-report/${reportID}`)} color='primary'>Edit Report</Button>
                            </Level.Item>
                            <Level.Item>
                                <Button onClick={()=>{setDeletePressed(true)}} color="primary">Delete</Button>
                            </Level.Item>
                        </Level>
                        {deleteMessage}
                    </Box>
                </Columns.Column>
                <Columns.Column size="half" >
                    <p className="is-size-5" id="desc-paragraph" style={{"whiteSpace": "pre-line"}}>{reportInfo.desc}</p>
                </Columns.Column>
                <Columns.Column>
                    <Box>
                        <NamedFieldLevel name="Route" content={contentCheck(reportInfo.route)} />
                        <NamedFieldLevel name="Location" content={contentCheck(reportInfo.loc)} />
                        <Level>
                            <Level.Side align='right'>
                                <Level.Item>
                                    <Heading heading className="has-text-weight-bold">Coordinates</Heading>
                                </Level.Item>
                                <Level.Item>
                                    <Heading heading>{contentCheck(reportInfo.coord[0])}, {contentCheck(reportInfo.coord[1])}</Heading>
                                </Level.Item>
                            </Level.Side>
                            <Level.Side align='right'>
                                <Level.Item>
                                    <Button color='primary' onClick={()=>setShowMap(true)}>Show on map</Button>
                                    <Modal closeOnBlur={true} showClose={false} show={showMap} onClose={()=>setShowMap(false)}>
                                        <Modal.Content>
                                            <Container>
                                                <Map id="modal-map" center={[reportInfo.coord[0], reportInfo.coord[1]]} zoom={12}>
                                                    <TileLayer
                                                        url="https://api.mapbox.com/styles/v1/{id}/tiles/{z}/{x}/{y}?access_token={accessToken}"
                                                        attribution='Map data  <a href="https://www.openstreetmap.org/">OpenStreetMap</a> contributors, <a href="https://creativecommons.org/licenses/by-sa/2.0/">CC-BY-SA</a>, Imagery (c) <a href="https://www.mapbox.com/">Mapbox</a>'
                                                        maxZoom={18}
                                                        id='mapbox/outdoors-v11'
                                                        accessToken= 'pk.eyJ1IjoidG9tbm92YWsxMyIsImEiOiJja2VseGtxeGwyYmU0MnJtejdiZmIydHd2In0.Wx7ng-I076vOfOb2qNnTkA'
                                                    />
                                                    <Marker position={[reportInfo.coord[0], reportInfo.coord[1]]} />
                                                </Map>
                                            </Container>
                                        </Modal.Content>
                                    </Modal>
                                </Level.Item>
                            </Level.Side>
                        </Level>

                        <Columns>
                            <Columns.Column size="half">
                                <Level id="centered-level-item">
                                    <Level.Item>
                                        <NamedField name="Discipline" content={contentCheck(reportInfo.disc)} />
                                    </Level.Item>
                                    <Level.Item>
                                        <NamedField name="Grade" content={gradeInfo ? gradeInfo.format(displayGradeSystem) + " " +  reportInfo.sev : "\u2014"} />
                                    </Level.Item>
                                </Level>
                            </Columns.Column>
                            <Columns.Column align='right' size="half">
                                <Dropdown 
                                    hoverable
                                    right
                                    value={displayGradeSystem}
                                    onChange={(value)=>{setDisplayGradeSystem(value)}}>
                                    <Dropdown.Item value='yds'>
                                        YDS
                                    </Dropdown.Item>
                                    <Dropdown.Item value='french'>
                                        French
                                    </Dropdown.Item>    
                                    <Dropdown.Item value='australian'>
                                        Australian
                                    </Dropdown.Item>
                                    <Dropdown.Item value='british'>
                                        British
                                    </Dropdown.Item>
                                    <Dropdown.Item value='south_african'>
                                        South African
                                    </Dropdown.Item>
                                    <Dropdown.Item value='hueco'>
                                        Hueco
                                    </Dropdown.Item>
                                    <Dropdown.Item value='font'>
                                        Fontainebleau
                                    </Dropdown.Item> 
                                </Dropdown>
                            </Columns.Column>   
                        </Columns>

                        <Level id="centered-level-item">
                            <Level.Item>
                                <NamedField name="Pitches" content={contentCheck(reportInfo.pitches)} />
                            </Level.Item>
                            <Level.Item>
                                <NamedField name="Total Length (m)" content={contentCheck(reportInfo.totalLength)} />
                            </Level.Item>
                            <Level.Item>
                                <NamedField name="Elevation (m)" content={contentCheck(reportInfo.elev)} />
                            </Level.Item>
                            <Level.Item>
                                <NamedField name="Aspect" content={contentCheck(reportInfo.aspect)} />
                            </Level.Item>
                        </Level>

                        <Box>
                            <NamedParagraph name="Gear" content={reportInfo.gear} />
                        </Box>

                        <Box>
                            <NamedParagraph name="Approach" content={reportInfo.approach} />
                        </Box>

                        <Box>
                            <NamedParagraph name="Descent" content={reportInfo.descent} />
                        </Box>

                        <Box>
                            <NamedParagraph name="Conditions" content={reportInfo.conditions} />
                        </Box>
                    </Box>
                </Columns.Column>
            </Columns>   
        </div>
        </> : null; //add loader


    //Event Handlers
    useEffect(() => {
        const fetchData = async () => {
            const result = await fetch(`/${username}/trip-reports/${reportID}`);
            const body = await result.json();
            setReportInfo(body);
        }
        fetchData();
    }, [username, reportID]);

    const handleUpvoteReport = async () => {
        const result = await fetch(`/api/${username}/trip-reports/${reportID}/upvote`, {
            method: 'post',
        });
        const body = await result.json();

        const updatedReport = body.reports.find(report => {
            return report.id === reportID;
        });
        setReportInfo(updatedReport);
    }

    const handleDeleteReport = async () => {
        await fetch(`/api/${username}/trip-reports/${reportID}/delete`, {
            method: 'post',
        });
    }

    //Render
    return(
        <>
        {content}
        </>
    );
}

export default ReportPage;