import React, { useState, useLayoutEffect, useRef } from 'react';

const calculateBoundingBoxes = children => {
    const boundingBoxes = {};

    React.Children.forEach(children, child => {
        const domNode = child.ref.current;

        if (domNode){
            const nodeBoundingBox = domNode.getBoundingClientRect();
            boundingBoxes[child.key] = nodeBoundingBox;
        }
    });

    return boundingBoxes;
}

function usePrevious(value) {
    const ref = useRef();
    useLayoutEffect(() => {
      ref.current = value;
    });
    return ref.current;
  }

const AnimateChildren = ({ children }) => {

    const [boundingBox, setBoundingBox] = useState({});
    const [prevBoundingBox, setPrevBoundingBox] = useState({});
    const prevChildren = usePrevious(children);
    const [scroll, setScroll] = useState(0);
    const prevScroll = usePrevious(scroll);

    useLayoutEffect(() => {
        const newBoundingBox = calculateBoundingBoxes(children);
        setBoundingBox(newBoundingBox);
        setScroll(window.scrollY);
    }, [children]);

    useLayoutEffect(() => {
        const prevBoundingBox = calculateBoundingBoxes(prevChildren);
        setPrevBoundingBox(prevBoundingBox);
    }, [prevChildren]);

    useLayoutEffect(() => {
        const hasPrevBoundingBox = Object.keys(prevBoundingBox).length;

        if (hasPrevBoundingBox) {
            React.Children.forEach(children, child => {
                const domNode = child.ref.current;
                const firstBox = prevBoundingBox[child.key];
                const lastBox = boundingBox[child.key];
                const scrollDelta = prevScroll - scroll;
                const changeInY = firstBox.top - lastBox.top + scrollDelta;

                if (changeInY) {
                    requestAnimationFrame(() => {
                        domNode.style.transform = `translateY(${changeInY}px)`;
                        domNode.style.transition = "transform 0s";

                        requestAnimationFrame(() => {
                            domNode.style.transform = "";
                            domNode.style.transition = "transform 500ms";
                        });
                    });
                }
            });
        }
    }, [boundingBox, prevBoundingBox, scroll, prevScroll, children]);

    return children;
}

export default AnimateChildren;