import React, { useState } from 'react';
import {
    Box,
    Card, 
    Content,
    Media
} from 'react-bulma-components';
import { getS3Image, s3Img } from '../components/helpers/s3Interface.js';
import './css/profileBox.css';

const AboutBox = ({ user }) => {
    //Declarations
    //const [userInfo, setUserInfo] = useState(null);
    const [coverImgSrc, setCoverImgSrc] = useState(null);
    const [firstLoad, setFirstLoad] = useState(true);
    //const [profileImgSrc, setProfileImgSrc] = useState(null);

    // //Event Handlers
    // useEffect(() => {
    //     const fetchData = async () => {
    //         const result = await fetch(`/${user}/trip-reports`);
    //         const body = await result.json();
    //         setUserInfo(body);
    //         }
    //         fetchData();
    //         getS3Image('mount-assiniboine.jpg').then(img => setCoverImgSrc(img));
    //         //getS3Image('Tom_Novak.JPG').then(img => setProfileImgSrc(img));
    // }, [user]);

    if(firstLoad) {
        getS3Image('mount-assiniboine.jpg').then(img => setCoverImgSrc(img));
        setFirstLoad(false);
    }

    //Render
    return (
        <>
        <Box>
            <Card>
                {s3Img(coverImgSrc)}
                <Card.Content>
                    <Media>
                        {/* <Media.Item renderAs="figure" position="left">
                            <Box style={{padding: '8px 8px 2px 8px'}}>
                                {s3Img(profileImgSrc, 96, {margin: '0px 0px 0px 0px'})}
                            </Box>
                        </Media.Item> */}
                        <Media.Item>
                            <Content>
                            <strong>About</strong>
                            <br />
                            <p className='is-size-6'>Hi! Thanks for checking out my project! This is a web application I made using the ReactJS framework, to display rock climbing trip reports. It's still a work in progress, but as of right now anyone can add, view and edit trip reports. Some features I found exciting were the dynamic resizing of images on click when viewing a report, the progress bar that adjusts as you move along the route, and the way that different logs reorganize themselves on the feed pages when different filters are used. Feel free to click around and send along any feedback you have!</p>
                            <br/>
                            <small>novakthomasa@gmail.com</small>
                            </Content>
                        </Media.Item>
                    </Media>
                </Card.Content>
            </Card>
        </Box>
        </>
    );
}

export default AboutBox;