import React, { useState, useEffect } from 'react';
import '../App.css'
import {
    Box,
    Columns,
    Level
} from 'react-bulma-components';
import AnalyticsTable from '../components/AnalyticsTable.js'
import ReportsList from '../components/ReportsList.js'
import { Form, Button } from 'react-bulma-components';
import UserMap from '../components/UserMap';
import Scroll from 'react-scroll';
const { Field, Control } = Form;

const scrollToContent = () => {
    let scroll = Scroll.animateScroll;
    scroll.scrollTo(57);
}

const ReportsFeedPage = ({ match, history }) => {
    //Declarations
    const user = match.params.user;
    const [pageState, setPageState] = useState(match.params.view);
    const [userInfo, setUserInfo] = useState(null);
    
    //passed down to child components to determine how entries will be sorted.
    //valid entries are 'date' 'pitch' 'length' 'elevation' 'time'
    const [sortBy, setSortBy] = useState('date');


    //Helper Functions
    const colorConversion = (state) => {
        return state ? "success" : "";
    }

    //scroll down to content on loading
    if(!userInfo) {
        scrollToContent();
    }

    //Conditional Renders

    const renderContent = (userInfo, pageState, sortBy) => {
        if (userInfo) {
            switch(pageState) {
                    case 'maps':
                        return (
                            <Columns>
                                <Columns.Column/>
                                <Columns.Column size='two-thirds'>
                                    <Box>
                                        <UserMap user={userInfo.user} />;
                                    </Box>
                                </Columns.Column>
                                <Columns.Column/>
                            </Columns>
                        );
                    case 'table':
                        return <AnalyticsTable userInfo={userInfo} sortBy={sortBy}/>;
                    case 'list':
                        return <ReportsList userInfo={userInfo} setUserInfo={setUserInfo} sortBy={sortBy} />;
                    default:
                        return null;
            }
        } else {
            return null;
        }
    }

 

    //Event Handlers
    useEffect(() => {
        const fetchData = async () => {
            const result = await fetch(`/${user}/picture-trip-reports`);
            const body = await result.json();
            setUserInfo(body);
        }
        fetchData();
    }, [user]);


    //Render
    return (
        <>
        {/* <Hero color="primary" size="small">
            <Hero.Body>
                <Container>
                <Heading size={3} align="left">
                    Trip Reports
                </Heading>
                <Heading subtitle align="left">
                    {userInfo ? undoFormatUserID(userInfo.user) : null}
                </Heading>
                </Container>
            </Hero.Body>
        </Hero> */}
        <div style={{margin: "1rem"}}>
            <Level renderAs="nav">
                <Level.Side align="left">
                    <Level.Item>
                        <Field kind="addons">
                            <Control>
                                <Button color={colorConversion(pageState === 'list')} onClick={() =>{setPageState('list')}}>List View</Button>
                            </Control>
                            <Control>
                                <Button color={colorConversion(pageState === 'maps')} onClick={() => {setPageState('maps');}}>Map View</Button>
                            </Control>
                            <Control>
                                <Button color={colorConversion(pageState === 'table')} onClick={() => {setPageState('table')}}>Table View</Button>
                            </Control>
                        </Field>
                    </Level.Item>
                </Level.Side>
                <Level.Side align="right">
                    <Level.Item>
                        <Field kind="addons">
                            <Control>
                                <Button color={colorConversion(sortBy === 'date')} onClick={() => setSortBy('date')}>Date</Button>
                            </Control>
                            <Control>
                                <Button color={colorConversion(sortBy === 'grade')} onClick={() => setSortBy('grade')}>Grade</Button>
                            </Control>
                            <Control>
                                <Button color={colorConversion(sortBy === 'pitch')} onClick={() => setSortBy('pitch')}>Pitch</Button>
                            </Control>
                            <Control>
                                <Button color={colorConversion(sortBy === 'length')} onClick={() => setSortBy('length')}>Length</Button>
                            </Control>
                            <Control>
                                <Button color={colorConversion(sortBy === 'time')} onClick={() => setSortBy('time')}>Time</Button>
                            </Control>
                            <Control>
                                <Button color={colorConversion(sortBy === 'elevation')} onClick={() => setSortBy('elevation')}>Elevation</Button>
                            </Control>
                        </Field>
                    </Level.Item>
                </Level.Side>
            </Level>
        </div>
        {renderContent(userInfo, pageState, sortBy)}
        </>
        );
}

export default ReportsFeedPage;