import React from 'react';
import { Heading } from 'react-bulma-components';

const NamedParagraph = ({ name, content, alignment='left' }) => {
    //Render
    return (
        <div align={alignment}>
            <Heading heading className="has-text-weight-bold">{name}</Heading>
            <p heading='true' className='is-size-7'>{content}</p>
        </div>
    );
}

export default NamedParagraph