import React from 'react';
import { Heading } from 'react-bulma-components';

const NamedField = (props) => {
    //Render
    return (
        <div>
        <Heading heading className="has-text-weight-bold">{props.name}</Heading>
        <Heading heading>{props.content}</Heading>
        </div>
    );
}

export default NamedField;