import React, { createRef, useState, useEffect } from 'react';
import { Heading } from 'react-bulma-components';
import {
    FeatureGroup,
    Map,
    Marker,
    Popup,
    TileLayer
} from 'react-leaflet';
import Leaflet from 'leaflet';
import { Link } from 'react-router-dom';
import NamedFieldLevel from '../components/NamedFieldLevel.js';

const UserMap = ({ user }) => {
    //Declarations
    const [userInfo, setUserInfo] = useState(null);
    const reports = userInfo ? userInfo.pitchReports : [];
    const [firstLoad, setFirstLoad] = useState(true);
    const mapRef = createRef();
    const groupRef = createRef();

    const corner1 = Leaflet.latLng(-90, -200)
    const corner2 = Leaflet.latLng(90, 200)
    const bounds = Leaflet.latLngBounds(corner1, corner2)

    const [activeReport, setactiveReport] = useState(null);

    //Event Handlers
    useEffect(() => {
        const fetchData = async () => {
            const result = await fetch(`/${user}/picture-trip-reports`);
            const body = await result.json();
            setUserInfo(body);
        }
        fetchData();
    }, [user]);

    useEffect(() => {    
        const noCoords = {
            _northEast: {
                lat: 0,
                lng: 0
            },
            _southWest: {
                lat: 0,
                lng: 0
            }
        }

        if (mapRef.current && groupRef.current) { //we will get inside just once when loading
            const map = mapRef.current.leafletElement;
            const layer = groupRef.current.leafletElement;
            if( (layer.getBounds()._northEast && !layer.getBounds()._northEast.equals(noCoords._northEast)) 
                    && (layer.getBounds()._southWest && !layer.getBounds()._southWest.equals(noCoords._southWest))
                    && firstLoad) {
                        map.fitBounds(layer.getBounds());
                        setFirstLoad(false);
            }
        }
    }, [mapRef, groupRef, firstLoad]);

    //Render
    return(
        <Map ref={mapRef} center={[51.09, -115.34]} zoom={10} minZoom={2} maxBounds={bounds} maxBoundsViscosity={1.0}>
            <TileLayer
                url="https://api.mapbox.com/styles/v1/{id}/tiles/{z}/{x}/{y}?access_token={accessToken}"
                attribution='Map data  <a href="https://www.openstreetmap.org/">OpenStreetMap</a> contributors, <a href="https://creativecommons.org/licenses/by-sa/2.0/">CC-BY-SA</a>, Imagery (c) <a href="https://www.mapbox.com/">Mapbox</a>'
                maxZoom={18}
                id='mapbox/outdoors-v11'
                accessToken= 'pk.eyJ1IjoidG9tbm92YWsxMyIsImEiOiJja2VseGtxeGwyYmU0MnJtejdiZmIydHd2In0.Wx7ng-I076vOfOb2qNnTkA'
            />
            <FeatureGroup ref={groupRef}>
                {reports.map(report => (
                    report.coord[0] !== "\u2014" && report.coord[1] !== "\u2014" && //funky conditional logic... not nice but wtv
                    <Marker
                        key={report.title}
                        position={[
                            report.coord[0],
                            report.coord[1]
                        ]}
                        onClick={() => setactiveReport(report)}
                    />
                ))}
            </FeatureGroup>

            {activeReport && (
                <Popup
                    position={[
                        activeReport.coord[0],
                        activeReport.coord[1]
                    ]}
                    onClose={() => setactiveReport(null)}
                >
                    <div id="homepage-popup">
                        <Link to={`/${user}/photo-report/${activeReport.id}`}>
                            <Heading heading className="has-text-weight-bold">{activeReport.title}</Heading>
                        </Link>
                        <NamedFieldLevel id="homepage-popup" name="Author" content={activeReport.author} />
                        <NamedFieldLevel id="homepage-popup" name="Location" content={activeReport.loc} />
                        <NamedFieldLevel id="homepage-popup" name="Route" content={activeReport.route} />
                        <NamedFieldLevel id="homepage-popup" name="Pitches" content={activeReport.pitches} />
                        <NamedFieldLevel id="homepage-popup" name="Summit Elevation (m)" content={activeReport.elev} />
                    </div>
                </Popup>
            )
            }
        </Map>
    );
}

export default UserMap;