import React from 'react';
import { Heading, Level } from 'react-bulma-components';

const NamedFieldLevel = (props) => {
    //Render
    return(
        <Level id={props.id}>
            <Level.Side align="left">
                <Level.Item>
                    <Heading heading className="has-text-weight-bold">{props.name}</Heading>
                </Level.Item>
            </Level.Side>
            <Level.Side align="right">
                <Level.Item>
                    <Heading heading>{props.content}</Heading>
                </Level.Item>
            </Level.Side>
        </Level>
    )
}

export default NamedFieldLevel;